import React, {Component} from 'react';
import './AnalysisPage.css';

import { BrowserRouter, Route, NavLink } from 'react-router-dom';
import Main from "../../spheres/main/main";
import PhysicalEnvironment from "../../spheres/physical-environment/PhysicalEnvironment";

const AnalysisPage = (props) => {

    return (
        <BrowserRouter>
        <div className="container-fluid">
            <h1> </h1>
            <ul className="nav nav-tabs">
                <li className="nav-item">
                    <NavLink className="nav-link nav-link0 active" to="/"></NavLink>
                </li>
                <li className="nav-item">
                    <NavLink className="nav-link nav-link_1" to="/career"></NavLink>
                </li>
                <li className="nav-item">
                    <NavLink className="nav-link nav-link_2" to="/relationships"></NavLink>
                </li>
                <li className="nav-item">
                    <NavLink className="nav-link nav-link_3" to="/finances"></NavLink>
                </li>
                <li className="nav-item">
                    <NavLink className="nav-link nav-link_4" to="/health"></NavLink>
                </li>
                <li className="nav-item">
                    <NavLink className="nav-link nav-link_5" to="/rest">/</NavLink>
                </li>
                <li className="nav-item">
                    <NavLink className="nav-link nav-link_6" to="/personal-growth"> </NavLink>
                </li>
                <li className="nav-item">
                    <NavLink className="nav-link nav-link_7" to="/physical-environment/">. </NavLink>
                </li>
                <li className="nav-item">
                    <NavLink className="nav-link nav-link_8" to="/persona"></NavLink>
                </li>
            </ul>
            <Route exact path="/" component={Main} />
            <Route path="/physical-environment" component={PhysicalEnvironment} />
        </div>
        </BrowserRouter>
    );
};

export default AnalysisPage;


